import React, { useState } from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter, Navigate, Route, Routes } from 'react-router-dom';
import { LoginPage } from './pages/login';
import { DashboardPage } from './pages/dashboard';
import { JoltPresentation } from './pages/presentations/JOLT/jolt-presentation'
import { JoltPublic } from './pages/presentations/JOLT/jolt-public'
import dotenv from 'dotenv'
dotenv.config()

function App() {
  const [user, setUser] = useState(null)
  return (
    <Routes>
      {!user && (
        <Route exact path={'/'} element={<LoginPage authenticate={() => setUser(true)} />} />
      )}
      {user && (
        <Route exact path={'/dashboard'} element={<DashboardPage logout={() => setUser(false)} />} />
      )}
      <Route exact path={'/jolt-presentation-public'} element={<JoltPublic host={user} />} />
      {user && (
      <Route exact path={'/jolt-presentation'} element={<JoltPresentation host={user} />} />
      )}
      <Route path='*' element={<Navigate to={user ? "/dashboard" : "/"} />} />
    </Routes>
  )
}

const rootElement = document.getElementById('root');
console.log(process.env.PUBLIC_URL)
ReactDOM.render(<BrowserRouter><App /></BrowserRouter>, rootElement)

