import './jolt-public.css'
import React, { useEffect, useState } from "react";
import { Link } from 'react-scroll'
import contents from '../../../resources/videos/Contents.mp4'
import headingGeneral from '../../../resources/videos/Heading_General.mp4'
import headingCharacters from '../../../resources/videos/Heading_Characters.mp4'
import headingEnd from '../../../resources/videos/Heading_End.mp4'
import headingRecommendation from '../../../resources/videos/Heading_reccomendation.mp4'
import author from '../../../resources/videos/author.mp4'
import general from '../../../resources/videos/General.mp4'
import live from '../../../resources/videos/Live.mp4'
import please from '../../../resources/videos/Please.mp4'
import plot from '../../../resources/videos/Plot.mp4'
import marco from '../../../resources/videos/Marco.mp4'
import characters from '../../../resources/videos/Characters.mp4'
import axios from 'axios'

export const JoltPublic = () => {
    const [Message, inpMessage] = useState("starts");
    const [inpUsername, setUsername] = useState("");
    const [fadeout, setfadeout] = useState("fadeIn");
    const [fadeWait, fadeoutWait] = useState("fadeOut");

    const [showQ1, setShowQ1] = useState("fadeOut");
    const [showQ2, setShowQ2] = useState("fadeOut");
    const [showQ3, setShowQ3] = useState("fadeOut");

    function vote(awnser, question) {
        setShowQ1('fadeOut')
        setShowQ2('fadeOut')
        setShowQ3('fadeOut')
        var QuestionID = ""
        axios.get('http://localhost:1801/jolt/question')
            .then(response => {
                QuestionID = response.data[0]._id
                if (awnser == "A1") {
                    var CurrentAwnsers = response.data[0].Awnser1
                    var NewAwnserCount = CurrentAwnsers + 1
                    var VoteAwnser = {
                        Awnser1: NewAwnserCount
                    }
                }

                if (awnser == "A2") {
                    var CurrentAwnsers = response.data[0].Awnser2
                    var NewAwnserCount = CurrentAwnsers + 1
                    var VoteAwnser = {
                        Awnser2: NewAwnserCount
                    }
                }

                if (awnser == "A3") {
                    var CurrentAwnsers = response.data[0].Awnser3
                    var NewAwnserCount = CurrentAwnsers + 1
                    var VoteAwnser = {
                        Awnser3: NewAwnserCount
                    }
                    if (question == "Q1") {
                        axios.get(`http://localhost:1801/jolt/user`) 
                            .then(response => {
                                response.data.map(users => {
                                    if(users.username == inpUsername) {
                                        const GetPoints = users.points + Math.floor(Math.random() * (100 - 50 + 1) + 50)
                                        const newPoints = {
                                            username: inpUsername,
                                            points: GetPoints
                                        }
                                        axios.post(`http://localhost:1801/jolt/user/update/${users._id}`, newPoints)
                                    }
                                })
                            })
                    }
                }

                if (awnser == "A4") {
                    var CurrentAwnsers = response.data[0].Awnser4
                    var NewAwnserCount = CurrentAwnsers + 1
                    var VoteAwnser = {
                        Awnser4: NewAwnserCount
                    }
                }
                axios.post(`http://localhost:1801/jolt/question/update/${QuestionID}`, VoteAwnser)
                    .then(() => {
                        if ( question == "Q1") {
                            Question2()
                        } else if ( question == "Q2" ) {
                            Question3()
                        } else if ( question == "Q3" ) {
                            End()
                        }
                    })


            })
        
        
    }

    function End() {
        inpMessage('ends')
        fadeoutWait('fadeIn')
    }

    function Question3() {
        const interval1 = setInterval(() => {
            axios.get('http://localhost:1801/jolt/status')
             .then(response => {
                if(!response.data[0].Question3 == 0) {
                    document.documentElement.style.setProperty('--question2-display', 'none')
                    fadeoutWait('fadeOut')
                    setShowQ3('fadeIn')
                    clearInterval(interval1);
                } else {
                    fadeoutWait('fadeIn')
                    setShowQ3('fadeOut')
                }
             })
          }, 2000);
        return () => clearInterval(interval1);
    }

    function Question2() {
        const interval1 = setInterval(() => {
            axios.get('http://localhost:1801/jolt/status')
             .then(response => {
                if(!response.data[0].Question2 == 0) {
                    document.documentElement.style.setProperty('--question1-display', 'none')
                    fadeoutWait('fadeOut')
                    setShowQ2('fadeIn')
                    clearInterval(interval1);
                } else {
                    fadeoutWait('fadeIn')
                    setShowQ2('fadeOut')
                }
             })
          }, 2000);
        return () => clearInterval(interval1);
    }

    function loggedin() {
        if(!inpUsername == '') {
            const newUser = {
                username: inpUsername,
                points: 0
            }
            axios.post('http://localhost:1801/jolt/user/add', newUser)
            setfadeout('fadeOut')
            fadeoutWait('fadeIn')
            const interval1 = setInterval(() => {
                axios.get('http://localhost:1801/jolt/status')
                 .then(response => {
                    if(!response.data[0].Question1 == 0) {
                        fadeoutWait('fadeOut')
                        setShowQ1('fadeIn')
                        inpMessage('updates')
                        clearInterval(interval1);
                    } else {
                        fadeoutWait('fadeIn')
                        setShowQ1('fadeOut')
                    }
                 })
              }, 2000);
            return () => clearInterval(interval1);

        } else {
            alert('Enter a Name!')
        }
    }

    useEffect(() => {
        const interval = setInterval(() => {
          axios.get('http://localhost:1801/jolt/status')
           .then(response => {
            if (!response.data[0].host == 0) {
                document.documentElement.style.setProperty('--quiz-status', 'none')
                document.documentElement.style.setProperty('--quiz-status-show', 'block')
            } else {
                document.documentElement.style.setProperty('--quiz-status', 'block') 
                document.documentElement.style.setProperty('--quiz-status-show', 'none')
            }
           })
        }, 2000);
        return () => clearInterval(interval);
    }, []);

    return (
    <div className="publicContainer">
        <div className="presentation">
            <Link className="nextpage" to="headingGeneral" spy={true} smooth={true}><video id="contents"autoPlay disablePictureInPicture muted src={contents} style={{zIndex: "-1"}}></video></Link>
            <Link className="nextpage" to="general" spy={true} smooth={true}><video id="headingGeneral"autoPlay disablePictureInPicture muted src={headingGeneral} style={{zIndex: "-1"}}></video></Link>
            <Link className="nextpage" to="author" spy={true} smooth={true}><video id="general"autoPlay disablePictureInPicture muted src={general} style={{zIndex: "-1"}}></video></Link>
            <Link className="nextpage" to="live" spy={true} smooth={true}><video id="author"autoPlay disablePictureInPicture muted src={author} style={{zIndex: "-1"}}></video></Link>
            <Link className="nextpage" to="headingCharacters" spy={true} smooth={true}><video id="live"autoPlay disablePictureInPicture muted src={live} style={{zIndex: "-1"}}></video></Link>
            <Link className="nextpage" to="plot" spy={true} smooth={true}><video id="headingCharacters"autoPlay disablePictureInPicture muted src={headingCharacters} style={{zIndex: "-1"}}></video></Link>
            <Link className="nextpage" to="marco" spy={true} smooth={true}><video id="plot"autoPlay disablePictureInPicture muted src={plot} style={{zIndex: "-1"}}></video></Link>
            <Link className="nextpage" to="characters" spy={true} smooth={true}><video id="marco"autoPlay disablePictureInPicture muted src={marco} style={{zIndex: "-1"}}></video></Link>
            <Link className="nextpage" to="headingRecommendation" spy={true} smooth={true}><video id="characters"autoPlay disablePictureInPicture muted src={characters} style={{zIndex: "-1"}}></video></Link>
            <Link className="nextpage" to="please" spy={true} smooth={true}><video id="headingRecommendation"autoPlay disablePictureInPicture muted src={headingRecommendation} style={{zIndex: "-1"}}></video></Link>
            <Link className="nextpage" to="headingQuiz" spy={true} smooth={true}><video id="please"autoPlay disablePictureInPicture muted src={please} style={{zIndex: "-1"}}></video></Link>
            <Link className="nextpage" to="headingGeneral" spy={true} smooth={true}><video id="headingEnd"autoPlay disablePictureInPicture muted src={headingEnd} style={{zIndex: "-1"}}></video></Link>
        </div>
        <div className="quiz">
            <div className="welcome">
                <h1>JOLT QUIZ</h1>
                <div className="input">
                    <input className={fadeout} type="text" placeholder='Name' value={inpUsername} onChange={(e) => setUsername(e.target.value)} />
                    <hr />
                    <h3 className={fadeout} onClick={() => loggedin()}>Submit</h3>
                    <h3 className={fadeWait} id='wait'>Please wait until the quiz {Message}!</h3>
                </div>
                <div id="Question1" className={showQ1}>
                    <div className="awnsers">
                        <p onClick={() => vote('A1', 'Q1')} id="awnser1" >1. Bernhard Beckett</p>
                        <p onClick={() => vote('A2', 'Q1')} id="awnser2" >2. Bernhard Butland</p>
                        <p onClick={() => vote('A3', 'Q1')} id="awnser3" >3. Bernard Beckett</p>
                        <p onClick={() => vote('A4', 'Q1')} id="awnser4">4. Baldwin Beckett</p>
                    </div>
                </div>
                <div id="Question2"className={showQ2}>
                    <div className="awnsers">
                        <p onClick={() => vote('A1', 'Q2')} id="awnser1" >1. With his friend in Germany</p>
                        <p onClick={() => vote('A2', 'Q2')} id="awnser2" >2. Abroad on a bike tour through europe</p>
                        <p onClick={() => vote('A3', 'Q2')} id="awnser3" >3. In new zealand on a volcano</p>
                        <p onClick={() => vote('A4', 'Q2')} id="awnser4">4. On top of the leaning tower of pisa</p>
                    </div>
                </div>
                <div id="Question3" className={showQ3}>
                    <div className="awnsers">
                        <p onClick={() => vote('A1', 'Q3')} id="awnser1" >1. The teenagers call the police and come home again</p>
                        <p onClick={() => vote('A2', 'Q3')} id="awnser2" >2. Everyone runs away and try to make contact with the city where the earthquake occurred</p>
                        <p onClick={() => vote('A3', 'Q3')} id="awnser3" >3. They hide the body, are suspected by the police, and end up in a psychiatric ward</p>
                        <p onClick={() => vote('A4', 'Q3')} id="awnser4">4. The killers catch Marko to have no witnesses and he ends up in a psychiatric ward</p>
                    </div>
            
                </div>
            </div>
        </div>
    </div>
    )
}