import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { ImCross } from 'react-icons/im'
import { BiTask, BiLogOut, BiMenu, BiTaskX } from 'react-icons/bi'
import { HiOutlinePresentationChartBar } from 'react-icons/hi'
import { RiFileShield2Line } from 'react-icons/ri'
import { MdOutlineSchool } from 'react-icons/md'
import { IoSettingsOutline } from 'react-icons/io5'
import { IoIosSchool, IoIosSunny } from 'react-icons/io'
import { GrFormAdd } from 'react-icons/gr'
import { BsCalendarPlus, BsJournalRichtext, BsFillGrid1X2Fill, BsFillCpuFill, BsFillMoonStarsFill } from 'react-icons/bs'
import { AiOutlineCloudServer } from 'react-icons/ai'
import logo from "../resources/images/logo.png"
import "../CSS/dashboard.css"

export const DashboardPage = ({ logout }) => {
    const [lightActive, setLightActive] = useState("");
    const [darkActive, setDarkActive] = useState("active");

    function toggleTheme(style) {
        if(style === "light") {
            setDarkActive('')
            setLightActive('active')
            document.documentElement.style.setProperty('--color-background', '#f6f6f9')
            document.documentElement.style.setProperty('--color-white', '#fff')
            document.documentElement.style.setProperty('--color-dark', '#363949')
            document.documentElement.style.setProperty('--color-dark-variant', '#677483')
            document.documentElement.style.setProperty('--color-light', 'rgba(132, 139, 200, 0.18)')
            document.documentElement.style.setProperty('--box-shadow', '0 2rem 3rem var(--color-light)')
        } else if(style === "dark") {
            setLightActive('')
            setDarkActive('active')
            document.documentElement.style.setProperty('--color-background', '#181a1e')
            document.documentElement.style.setProperty('--color-white', '#202528')
            document.documentElement.style.setProperty('--color-dark', '#edeffd')
            document.documentElement.style.setProperty('--color-dark-variant', '#a3bdcc')
            document.documentElement.style.setProperty('--color-light', 'rgba(0, 0, 0, 0.4)')
            document.documentElement.style.setProperty('--box-shadow', '0 2rem 3rem var(--color-light)')
        }
    }

    function showMobileMenu() {
        document.documentElement.style.setProperty('--display-menu', 'block')
    }

    function hideMobileMenu() {
        document.documentElement.style.setProperty('--display-menu', 'none')
    }

    //LOGOUT
    const navigate = useNavigate()
    function logoutl() {
        logout()
        navigate('/')
    }

    //Redirect
    function pageRedirect(page) {
        toggleTheme('light')
        navigate(page)
    }

    //Toggle Theme on load
    useEffect(()=>{
        toggleTheme("light")
    }, [])

    return (
        <div className="react">
            <div className="container">
                <aside>
                    <div className="top">
                        <div className="logo">
                            <img src={logo} alt="Logo" />
                            <h2>PERSON<span className="danger">AL</span></h2>
                        </div>
                        <div className="close" id="close-btn" onClick={hideMobileMenu}>
                            <ImCross className="material-icons-sharp" />
                        </div>
                    </div>

                    <div className="sidebar">
                        <a href="#" className="active">
                            <span><BsFillGrid1X2Fill className="material-icons-sharp" /></span>
                            <h3>Dashboard</h3>
                        </a>
                        <a href="#">
                            <span><HiOutlinePresentationChartBar className="material-icons-sharp" /></span>
                            <h3>Präsentationen</h3>
                        </a>
                        <a href="#">
                            <span><BiTask className="material-icons-sharp" /></span>
                            <h3>Hausaufgaben</h3>
                            <span className="message-count">3</span>
                        </a>
                        <a href="#">
                            <span><BsCalendarPlus className="material-icons-sharp" /></span>
                            <h3>Stundenplan</h3>
                        </a>
                        <a href="#">
                            <span><MdOutlineSchool className="material-icons-sharp" /></span>
                            <h3>Noten</h3>
                        </a>
                        <a href="#">
                            <span><BsJournalRichtext className="material-icons-sharp" /></span>
                            <h3>Vokabeln</h3>
                        </a>
                        <a href="#">
                            <span><RiFileShield2Line className="material-icons-sharp" /></span>
                            <h3>Datein</h3>
                        </a>
                        <a href="#">
                            <span><IoSettingsOutline className="material-icons-sharp" /></span>
                            <h3>Einstellungen</h3>
                        </a>
                        <a href="#" onClick={logoutl}>
                            <span><BiLogOut className="material-icons-sharp" /></span>
                            <h3>Ausloggen</h3>
                        </a>
                    </div>
                </aside>
                
                <main>
                    <h1>Dashboard</h1>
                    <div className="date">
                        <input type="date" />
                    </div>
                    <div className="insights">
                        <div className="education">
                            <span className="material-icons-sharp"><IoIosSchool /></span>
                            <div className="middle">
                                <div className="left">
                                    <h3>Vortschritt</h3>
                                    <h1>43 / 132</h1>
                                </div>
                                <div className="progress">
                                    <svg>
                                        <circle cx='38' cy='38' r='36'></circle>
                                    </svg>
                                    <div className="number">
                                        <p>68.3%</p>
                                    </div>
                                </div>
                            </div>
                            <small className="text-muted">Letzter Monat</small>
                        </div>
                        <div className="storage">
                            <span className="material-icons-sharp"><AiOutlineCloudServer /></span>
                            <div className="middle">
                                <div className="left">
                                    <h3>Storage</h3>
                                    <h1>325GB</h1>
                                </div>
                                <div className="progress">
                                    <svg>
                                        <circle cx='38' cy='38' r='36'></circle>
                                    </svg>
                                    <div className="number">
                                        <p>57.5%</p>
                                    </div>
                                </div>
                            </div>
                            <small className="text-muted">Aktuell</small>
                        </div>
                        <div className="server">
                            <span className="material-icons-sharp"><BsFillCpuFill /></span>
                            <div className="middle">
                                <div className="left">
                                    <h3>Server</h3>
                                    <h1>1.4 MHz</h1>
                                </div>
                                <div className="progress">
                                    <svg>
                                        <circle cx='38' cy='38' r='36'></circle>
                                    </svg>
                                    <div className="number">
                                        <p>14.9%</p>
                                    </div>
                                </div>
                            </div>
                            <small className="text-muted">Aktuell</small>
                        </div>

                    </div>
                    <div className="presentations">
                        <h2>Presentationen</h2>
                        <table>
                            <thead>
                                <tr>
                                    <th>Thema / Name</th>
                                    <th>Fach</th>
                                    <th className="Status">Status</th>
                                    <th>Datum</th>
                                    <th></th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td>Battle of the books - Jolt</td>
                                    <td>Englisch</td>
                                    <td className="Status warning">Ausstehend</td>
                                    <td>17. April</td>
                                    <td className="showButton primary" onClick={() => pageRedirect('/jolt-presentation')}>Anzeigen</td>
                                </tr>
                                <tr>
                                    <td>Hochspannungsleitungen</td>
                                    <td>Physik</td>
                                    <td className="Status warning">Ausstehend</td>
                                    <td>10. April</td>
                                    <td className="showButton primary" onClick={() => pageRedirect('/jolt-presentation-public')}>Anzeigen</td>
                                </tr>
                                <tr>
                                    <td>Gestagen</td>
                                    <td>Biologie</td>
                                    <td className="Status success">Beendet</td>
                                    <td>3. Februar</td>
                                    <td className="showButton primary">Anzeigen</td>
                                </tr>
                            </tbody>
                        </table>
                        <a href="#">Alle anzeigen</a>
                    </div>
                </main>

                <div className="right">
                    <div className="top">
                        <button id="menu-btn" onClick={showMobileMenu}>
                            <span className="material-icons-sharp"><BiMenu /></span>
                        </button>
                        <div className="theme-toggler">
                            <span className={lightActive} onClick={() => toggleTheme("light")}><IoIosSunny /></span>
                            <span style={{fontSize: "1rem"}} className={darkActive} onClick={() => toggleTheme("dark")}><BsFillMoonStarsFill /></span>
                        </div>
                        <div className="profile">
                            <div className="info">
                                <p>Hey, <b>Jonas</b></p>
                                <small className="text-muted">Admin</small>
                            </div>
                            <div className="profile-photo">
                                <img src={logo} alt="ProfilBild" />
                            </div>
                        </div>
                    </div>

                    <div className="planner">
                        <h2>Stundenplan</h2>
                        <div className="updates">
                            <div className="update">
                                <div className="message">
                                    <p><b>Mathematik</b></p>
                                    <p> 7:55 - 9:15 UHR</p>
                                    <small className="text-muted">Herr Kabilka</small>
                                </div>
                            </div>
                            <div className="update">
                                <div className="message">
                                    <p><b>Musik</b></p>
                                    <p> 7:55 - 9:15 UHR</p>
                                    <small className="text-muted">Herr Kabilka</small>
                                </div>
                            </div>
                        
                            <div className="update">
                                <div className="message">
                                    <p><b>Spanisch</b></p>
                                    <p> 7:55 - 9:15 UHR</p>
                                    <small className="text-muted">Herr Kabilka</small>
                                </div>
                            </div>
                        </div>
                        
                    </div>
                    <div className="homework">
                        <h2>Hausaufgaben</h2>
                        <div className="item">
                            <div className="icon">
                                <span className="material-icons-sharp"><BiTaskX /></span>
                            </div>
                            <div className="right">
                                <div className="info">
                                    <h3>Englisch</h3>
                                    <small className="text-muted">Buchpräsentation JOLT</small>
                                </div>
                                <h3>03.März.2022</h3>
                            </div>
                        </div>
                        <div className="item">
                            <div className="icon">
                                <span className="material-icons-sharp"><BiTaskX /></span>
                            </div>
                            <div className="right">
                                <div className="info">
                                    <h3>Mathematik</h3>
                                    <small className="text-muted">Buch S.120 Nr.3</small>
                                </div>
                                <h3>03.März.2022</h3>
                            </div>
                        </div>
                        <div className="item">
                            <div className="icon">
                                <span className="material-icons-sharp"><BiTaskX /></span>
                            </div>
                            <div className="right">
                                <div className="info">
                                    <h3>Englisch</h3>
                                    <small className="text-muted">Buchpräsentation JOLT</small>
                                </div>
                                <h3>03.März.2022</h3>
                            </div>
                        </div>
                        <div className="item add-task">
                            <div>
                                
                                <span className="plus"><GrFormAdd color="#7380ec"/></span>
                                <h3>Hinzufügen</h3>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

