import React, { PureComponent, useEffect, useState } from "react";
import { Link } from 'react-scroll'
import welcomeback from "../../../resources/images/JOLT/Welcome-Back.png"
import welcomesky from "../../../resources/images/JOLT/Welcome-Sky.png"
import welcomecity from "../../../resources/images/JOLT/Welcome-City.png"
import welcomemoon from "../../../resources/images/JOLT/Welcome-Moon.png"
import welcomeman from "../../../resources/images/JOLT/Welcome-Man.png"
import welcomelight from "../../../resources/images/JOLT/Welcome-Light.png"
import welcomebushes from "../../../resources/images/JOLT/Welcome-Bushes.png"
import welcomeborder from "../../../resources/images/JOLT/Welcome-Border.png"
import welcomestars from "../../../resources/images/JOLT/Welcome-Stars.png"
import qrcode from "../../../resources/images/JOLT/qrcode.png"
import quizback from "../../../resources/images/JOLT/Background.png"
import quizqr from "../../../resources/images/JOLT/quizqr.png"
import contents from '../../../resources/videos/Contents.mp4'
import headingGeneral from '../../../resources/videos/Heading_General.mp4'
import headingCharacters from '../../../resources/videos/Heading_Characters.mp4'
import headingEnd from '../../../resources/videos/Heading_End.mp4'
import headingQuiz from '../../../resources/videos/Heading_Quiz.mp4'
import headingRecommendation from '../../../resources/videos/Heading_reccomendation.mp4'
import author from '../../../resources/videos/author.mp4'
import general from '../../../resources/videos/General.mp4'
import live from '../../../resources/videos/Live.mp4'
import please from '../../../resources/videos/Please.mp4'
import plot from '../../../resources/videos/Plot.mp4'
import sources from '../../../resources/videos/Sources.mp4'
import bernardBeckett from '../../../resources/images/JOLT/bernard-beckett.jpg'
import tour from '../../../resources/images/JOLT/tour.jpg'
import marco from '../../../resources/videos/Marco.mp4'
import characters from '../../../resources/videos/Characters.mp4'
import './jolt-presentation.css'
import axios from 'axios'
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';

export const JoltPresentation = ({ host }) => {

  const [data1, setData1] = useState([]);
  const [data2, setData2] = useState([]);
  const [data3, setData3] = useState([]);

  const [GameWinner, setGameWinner] = useState('');
  const [GameWinnerPoints, setGameWinnerPoints] = useState('');

  const [fadeOutStartQuiz, setfadeOutStartQuiz] = useState('fadeIn');
  const [fadeQuestion1, setfadeQuestion1] = useState('fadeOut');

  const [Timer, setTimer] = useState(30);
  const [GlobalAnswers, setGlobalAnswers] = useState(0);
  const [offsetY, setOffsetY] = useState(0);
  const handleScroll = () => setOffsetY(window.pageYOffset);

  useEffect(() => {
    window.addEventListener('scroll', handleScroll)
    return() => window.removeEventListener('scroll', handleScroll)
  }, []);

  const [Users, setUserCount] = useState(0);

  function activateQuiz() {
    const hostOnline = {
        host: 1,
        Question1: 0,
        Question2: 0,
        Question3: 0
    }
    axios.post("http://localhost:1801/jolt/status/add", hostOnline)
  }

  function Question1() {
    const newQuestions = {
      Awnser1: 0,
      Awnser2: 0,
      Awnser3: 0,
      Awnser4: 0
    }
    axios.post('http://localhost:1801/jolt/question/add', newQuestions)
    var StatusID = "0"
    axios.get('http://localhost:1801/jolt/status')
      .then(response => {
        console.log(response)
       StatusID = response.data[0]._id
       var newStatus = {
        host: 1,
        Question1: 1,
        Question2: 0,
        Question3: 0,
      }
      console.log(StatusID)
      axios.post(`http://localhost:1801/jolt/status/update/${StatusID}`, newStatus)
      document.documentElement.style.setProperty('--quiz-start-display', 'none')
      setfadeOutStartQuiz('fadeOut')
      setfadeQuestion1('fadeIn')
      var TimeLeft = 30
      var Timer = setInterval(() => {
        if ( TimeLeft == 0 ) {
          clearTimeout(Timer)
          axios.get('http://localhost:1801/jolt/question')
            .then(response => {
              setData1([
                {name: '\"Bernhard Beckett\"', Votes: response.data[0].Awnser1},
                {name: '\"Bernhard Butland\"', Votes: response.data[0].Awnser2},
                {name: '\"Bernard Beckett\"', Votes: response.data[0].Awnser3},
                {name: '\"Baldwin Beckett\"', Votes: response.data[0].Awnser4}
              ])
            })
        } else {
          TimeLeft--
          setTimer(TimeLeft)
          axios.get('http://localhost:1801/jolt/question')
            .then(response => {
              var result = response.data[0].Awnser1 + response.data[0].Awnser2 + response.data[0].Awnser3 + response.data[0].Awnser4
              setGlobalAnswers(result)
            })
        }
      }, 1000)
    })
  }

  function Question2() {
    axios.delete('http://localhost:1801/jolt/question')
      .then(() => {
        const newQuestions = {
          Awnser1: 0,
          Awnser2: 0,
          Awnser3: 0,
          Awnser4: 0
        }
        axios.post('http://localhost:1801/jolt/question/add', newQuestions)
        var StatusID = "0"
        axios.get('http://localhost:1801/jolt/status')
          .then(response => {
            console.log(response)
           StatusID = response.data[0]._id
           var newStatus = {
            host: 1,
            Question1: 1,
            Question2: 1,
            Question3: 0,
          }
          console.log(StatusID)
          axios.post(`http://localhost:1801/jolt/status/update/${StatusID}`, newStatus)
          var TimeLeft = 30
          var Timer = setInterval(() => {
            if ( TimeLeft == 0 ) {
              clearTimeout(Timer)
              axios.get('http://localhost:1801/jolt/question')
                .then(response => {
                  setData2([
                    {name: '\"Awnser 1\"', Votes: response.data[0].Awnser1},
                    {name: '\"Awnser 2\"', Votes: response.data[0].Awnser2},
                    {name: '\"Awnser 3\"', Votes: response.data[0].Awnser3},
                    {name: '\"Awnser 4\"', Votes: response.data[0].Awnser4}
                  ])
                })
            } else {
              TimeLeft--
              setTimer(TimeLeft)
              axios.get('http://localhost:1801/jolt/question')
                .then(response => {
                  var result = response.data[0].Awnser1 + response.data[0].Awnser2 + response.data[0].Awnser3 + response.data[0].Awnser4
                  setGlobalAnswers(result)
                })
            }
          }, 1000)
        })
      })
  }

  function Question3() {
    axios.delete('http://localhost:1801/jolt/question')
      .then(() => {
        const newQuestions = {
          Awnser1: 0,
          Awnser2: 0,
          Awnser3: 0,
          Awnser4: 0
        }
        axios.post('http://localhost:1801/jolt/question/add', newQuestions)
        var StatusID = "0"
        axios.get('http://localhost:1801/jolt/status')
          .then(response => {
            console.log(response)
           StatusID = response.data[0]._id
           var newStatus = {
            host: 1,
            Question1: 1,
            Question2: 1,
            Question3: 1,
          }
          console.log(StatusID)
          axios.post(`http://localhost:1801/jolt/status/update/${StatusID}`, newStatus)
          var TimeLeft = 30
          var Timer = setInterval(() => {
            if ( TimeLeft == 0 ) {
              clearTimeout(Timer)
              axios.get('http://localhost:1801/jolt/question')
                .then(response => {
                  setData3([
                    {name: '\"Awnser 1\"', Votes: response.data[0].Awnser1},
                    {name: '\"Awnser 2\"', Votes: response.data[0].Awnser2},
                    {name: '\"Awnser 3\"', Votes: response.data[0].Awnser3},
                    {name: '\"Awnser 4\"', Votes: response.data[0].Awnser4}
                  ])
                })
            } else {
              TimeLeft--
              setTimer(TimeLeft)
              axios.get('http://localhost:1801/jolt/question')
                .then(response => {
                  var result = response.data[0].Awnser1 + response.data[0].Awnser2 + response.data[0].Awnser3 + response.data[0].Awnser4
                  setGlobalAnswers(result)
                })
            }
          }, 1000)
        })
      })
  }

  function Winner() {
    axios.get('http://localhost:1801/jolt/user')
      .then(response => {
        var Winner = ""
        var highscore = 0
        response.data.map(user => {
          if ( user.points > highscore) {
            highscore = user.points
            Winner = user.username
          }
        })
        setGameWinner(Winner)
        setGameWinnerPoints(highscore)
      })
  }

  useEffect(() => {
    axios.delete('http://localhost:1801/jolt/user')
    axios.delete('http://localhost:1801/jolt/status')
    axios.delete('http://localhost:1801/jolt/question')
    const interval = setInterval(() => {
      axios.get('http://localhost:1801/jolt/user')
       .then(response => {
        if (response.data.length > 0) {
          var Usercount = 0
          response.data.map(user => {
            Usercount++
          })
          setUserCount(Usercount)
        }
       })
    }, 5000);
  
    return () => clearInterval(interval);
  }, []);

  return (
    <div className="main">
      <div className="presentation">
        <div className="WelcomeSlide">
          <div className="text">
            <h3 style={{ transform: `translateY(${270 + offsetY * 0.4}px)`}} >Battle of the books</h3>
            <h1 style={{ transform: `translateY(${270 + offsetY * 0.3}px)`}} >JOLT <span className="lowerTitle">Bernard Beckett</span></h1>
          </div>
          <div className="images" >
            <img src={welcomeback} alt="Background" className="welcomeback" />
            <img style={{ transform: `translateY(${offsetY < 500 ? offsetY * 0.5 : 0}px)`}} src={welcomemoon} alt="Background" className="welcomemoon" />
            <img style={{ transform: `translateX(${offsetY > 50 && offsetY < 100 ||  offsetY > 150 && offsetY < 200? `-${offsetY * 0.05}` : offsetY * 0.05}px)`}} src={welcomecity} alt="Background" className="welcomecity" />
            <img style={{ transform: `translateY(-${offsetY * 0.5}px)`}} src={welcomesky} alt="Background" className="welcomesky" />
            <img src={welcomeman} alt="Background" className="welcomeman" />
            <img src={welcomelight} alt="Background" className="welcomelight" />
            <Link className="nextpage" to="qr" spy={true} smooth={true}><img src={welcomebushes} alt="Background" className="welcomebushes" /></Link>
            <img style={{ transform: `translateX(${offsetY < 500 ? offsetY * 0.3 : 1}px)`}} src={welcomestars} alt="Background" className="welcomestars" />
          </div>
        </div>
        <div className="Gliederung">
          <div className="title">
            <img src={welcomeborder} alt="Background" className="welcomeborder" />
            <Link className="nextpage" to="contents" spy={true} smooth={true}><img id="qr" style={{ transform: `translateX(${(offsetY * 0.3) - 300}px)`}} src={qrcode} alt="qrcode" /></Link>
          </div>
          <Link className="nextpage" to="headingGeneral" spy={true} smooth={true}><video id="contents"autoPlay disablePictureInPicture muted src={contents} style={{zIndex: "-1"}}></video></Link>
          <Link className="nextpage" to="general" spy={true} smooth={true}><video id="headingGeneral"autoPlay disablePictureInPicture muted src={headingGeneral} style={{zIndex: "-1"}}></video></Link>
          <Link className="nextpage" to="author" spy={true} smooth={true}><video id="general"autoPlay disablePictureInPicture muted src={general} style={{zIndex: "-1"}}></video></Link>
          <Link className="nextpage" to="live" spy={true} smooth={true}><video id="author"autoPlay disablePictureInPicture muted src={author} style={{zIndex: "-1"}}></video></Link>
          <Link className="nextpage" to="headingCharacters" spy={true} smooth={true}><video id="live"autoPlay disablePictureInPicture muted src={live} style={{zIndex: "-1"}}></video></Link>
          <Link className="nextpage" to="plot" spy={true} smooth={true}><video id="headingCharacters"autoPlay disablePictureInPicture muted src={headingCharacters} style={{zIndex: "-1"}}></video></Link>
          <Link className="nextpage" to="marco" spy={true} smooth={true}><video id="plot"autoPlay disablePictureInPicture muted src={plot} style={{zIndex: "-1"}}></video></Link>
          <Link className="nextpage" to="characters" spy={true} smooth={true}><video id="marco"autoPlay disablePictureInPicture muted src={marco} style={{zIndex: "-1"}}></video></Link>
          <Link className="nextpage" to="headingRecommendation" spy={true} smooth={true}><video id="characters"autoPlay disablePictureInPicture muted src={characters} style={{zIndex: "-1"}}></video></Link>
          <Link className="nextpage" to="please" spy={true} smooth={true}><video id="headingRecommendation"autoPlay disablePictureInPicture muted src={headingRecommendation} style={{zIndex: "-1"}}></video></Link>
          <Link className="nextpage" to="headingQuiz" spy={true} smooth={true}><video id="please"autoPlay disablePictureInPicture muted src={please} style={{zIndex: "-1"}}></video></Link>
          <Link className="nextpage" to="quiz" onClick={() => activateQuiz()} spy={true} smooth={true}><video id="headingQuiz"autoPlay disablePictureInPicture muted src={headingQuiz} style={{zIndex: "-1"}}></video></Link>
          <div id="quiz" className="quizAuthor">
            <div className="content">
              <Link className="nextpage" to="quizAuthorResults" spy={true} smooth={true}><h1>JOLT QUIZ</h1></Link>
              <h3 id="questionNumber" className={fadeQuestion1} >-- Question 1 --</h3>
              <h4 id="Question" className={fadeQuestion1}>How is the author of the book "JOLT" called? Awnsers: {GlobalAnswers}</h4>
              <h4 id="Timer" className={fadeQuestion1} >Timer: {Timer} seconds remaining!</h4>
              <h4 id="Users" className={fadeOutStartQuiz}>Users: {Users}</h4>
              <img id="qr" className={fadeOutStartQuiz} src={quizqr} alt="qrcode" />
              <h3 id="StartQuiz" onClick={() => Question1()} className={fadeOutStartQuiz} >Start Quiz</h3>
              <div className={fadeQuestion1} id="awnsers">
                <p id="awnser1">1. Bernhard Beckett</p>
                <p id="awnser2">2. Bernhard Butland</p>
                <p id="awnser3">3. Bernard Beckett</p>
                <p id="awnser4">4. Baldwin Beckett</p>
              </div>
              <Link className="nextpage" to="quizAuthorResults" spy={true} smooth={true}><button className="showResults">Show Results</button></Link>
            </div>
            <Link className="nextpage" to="quizAuthorResults" spy={true} smooth={true}><img className="ImageBack" src={quizback} alt="Background" /></Link>
          </div>
          <div id="quizAuthorResults" className="quizAuthorResults">
            <div className="content">
            <Link className="nextpage" to="writtenQuiz" spy={true} smooth={true}><h1 onClick={() => Question2()}>JOLT QUIZ</h1></Link>
              <h3 id="Results" >-- RESULTS --</h3>
              <h4 id="Question">How is the author of the book "JOLT" called?</h4>
              <div className="results">
                <ResponsiveContainer id="BarChart1" width="50%" height={400}>
                  <BarChart width={500} height={300} data={data1} margin={{ top: 20,right: 30,left: 400,bottom: 5,}}barSize={20}>
                    <XAxis dataKey="name" scale="point" padding={{ left: 10, right: 10 }} />
                    <YAxis />
                    <Tooltip />
                    <Legend />
                    <CartesianGrid strokeDasharray="3 3" />
                    <Bar dataKey="Votes" fill="#af6c41" background={{ fill: '#eee' }} />
                  </BarChart>
                </ResponsiveContainer>
                <img src={bernardBeckett} alt="Portrait" />
              </div>
            </div>
            <Link className="nextpage" to="writtenQuiz" spy={true} smooth={true}><img className="ImageBack" src={quizback} alt="Background" /></Link>
          </div>
          <div className="writtenQuiz">
            <div id="quiz" className="quizAuthor">
              <div className="content">
                <Link className="nextpage" to="writtenQuizresults" spy={true} smooth={true}><h1>JOLT QUIZ</h1></Link>
                <h3 id="questionNumber" className={fadeQuestion1} >-- Question 2 --</h3>
                <h4 id="Question" className={fadeQuestion1}>Where did the author write the book Jolt? Awnsers: {GlobalAnswers}</h4>
                <h4 id="Timer" className={fadeQuestion1} >Timer: {Timer} seconds remaining!</h4>
                <div className={fadeQuestion1} id="awnsers2">
                  <p id="awnser1">1. With his friend in Germany</p>
                  <p id="awnser2">2. Abroad on a bike tour through europe</p>
                  <p id="awnser3">3. In new zealand on a volcano</p>
                  <p id="awnser4">4. On top of the leaning tower of pisa</p>
                </div>
                <Link className="nextpage" to="writtenQuizresults" spy={true} smooth={true}><button className="showResults">Show Results</button></Link>
              </div>
              <Link className="nextpage" to="writtenQuizresults" spy={true} smooth={true}><img className="ImageBack" src={quizback} alt="Background" /></Link>
            </div>
          </div>
          <div className="writtenQuizresults">
            <div id="quizAuthorResults" className="quizAuthorResults">
              <div className="content">
              <Link className="nextpage" to="nextQuiz" spy={true} smooth={true}><h1 onClick={() => Question3()}>JOLT QUIZ</h1></Link>
                <h3 id="Results" >-- RESULTS --</h3>
                <h4 id="Question">Where did the author write the book Jolt?</h4>
                <div className="results">
                  <ResponsiveContainer id="BarChart1" width="50%" height={400}>
                    <BarChart width={500} height={300} data={data2} margin={{ top: 20,right: 30,left: 400,bottom: 5,}}barSize={20}>
                      <XAxis dataKey="name" scale="point" padding={{ left: 10, right: 10 }} />
                      <YAxis />
                      <Tooltip />
                      <Legend />
                      <CartesianGrid strokeDasharray="3 3" />
                      <Bar dataKey="Votes" fill="#af6c41" background={{ fill: '#eee' }} />
                    </BarChart>
                  </ResponsiveContainer>
                  <img src={tour} alt="Portrait" />
                </div>
              </div>
              <Link className="nextpage" to="nextQuiz" spy={true} smooth={true}><img className="ImageBack" src={quizback} alt="Background" /></Link>
            </div>
          </div>
          <div className="nextQuiz">
            <div id="quiz" className="quizAuthor">
              <div className="content">
                <Link className="nextpage" to="nextQuizresults" spy={true} smooth={true}><h1>JOLT QUIZ</h1></Link>
                <h3 id="questionNumber" className={fadeQuestion1} >-- Question 3 --</h3>
                <h4 id="Question" className={fadeQuestion1}>How will the story continue? Awnsers: {GlobalAnswers}</h4>
                <h4 id="Timer" className={fadeQuestion1} >Timer: {Timer} seconds remaining!</h4>
                <div className={fadeQuestion1} id="awnsers2">
                  <p id="awnser1">1. The teenagers call the police and come home again</p>
                  <p id="awnser2">2. Everyone runs away and try to make contact with the city where the earthquake occurred</p>
                  <p id="awnser3">3. They hide the body, are suspected by the police, and end up in a psychiatric ward</p>
                  <p id="awnser4">4. The killers catch Marko to have no witnesses and he ends up in a psychiatric ward</p>
                </div>
                <Link className="nextpage" to="nextQuizresults" spy={true} smooth={true}><button className="showResults">Show Results</button></Link>
              </div>
              <Link className="nextpage" to="nextQuizresults" spy={true} smooth={true}><img className="ImageBack" src={quizback} alt="Background" /></Link>
            </div>
          </div>
          <div className="nextQuizresults">
            <div id="quizAuthorResults" className="quizAuthorResults">
              <div className="content">
                <Link className="nextpage" to="Winnerresults" spy={true} smooth={true}><h1 onClick={() => Winner()}>JOLT QUIZ</h1></Link>
                <h3 id="Results" >-- RESULTS --</h3>
                <h4 id="Question">How will the story continue?</h4>
                <div className="results">
                  <ResponsiveContainer id="BarChart1" width="50%" height={400}>
                    <BarChart width={500} height={300} data={data3} margin={{ top: 20,right: 30,left: 400,bottom: 5,}}barSize={20}>
                      <XAxis dataKey="name" scale="point" padding={{ left: 10, right: 10 }} />
                      <YAxis />
                      <Tooltip />
                      <Legend />
                      <CartesianGrid strokeDasharray="3 3" />
                      <Bar dataKey="Votes" fill="#af6c41" background={{ fill: '#eee' }} />
                    </BarChart>
                  </ResponsiveContainer>
                  <img src={tour} alt="Portrait" />
                </div>
              </div>
              <Link className="nextpage" to="Winnerresults" spy={true} smooth={true}><img className="ImageBack" src={quizback} alt="Background" /></Link>
            </div>
          </div>
          <div className="Winnerresults">
            <div id="quizAuthorResults" className="quizAuthorResults">
              <div className="content">
                <Link className="nextpage" to="sources" spy={true} smooth={true}><h1>JOLT QUIZ</h1></Link>
                <h3 id="Results" >-- RESULTS --</h3>
                <h4 id="Question">THE WINNER:</h4>
                <h2 id="WinnerTag">{GameWinner} // {GameWinnerPoints} Points</h2>
              </div>
              <Link className="nextpage" to="sources" spy={true} smooth={true}><img className="ImageBack" src={quizback} alt="Background" /></Link>
            </div>
          </div>
          <Link className="nextpage" to="headingEnd" spy={true} smooth={true}><video id="sources"autoPlay disablePictureInPicture muted src={sources} style={{zIndex: "-1"}}></video></Link>
          <Link className="nextpage" to="headingGeneral" spy={true} smooth={true}><video id="headingEnd"autoPlay disablePictureInPicture muted src={headingEnd} style={{zIndex: "-1"}}></video></Link>
      </div>

    </div>  
    <div className="tablet">
      
    </div>
  </div>
  )
}